export default {
	props:{
		show:Boolean,
		sharedata:Object
	},
	data() {
		return {
			title:"",
			shareText: '',
			href:"",
			image: '',
			shareType:1,//1文字 2图片 0图文 5小程序
			providerList: []
		}
	},
	watch: {
		sharedata(newValue, oldValue) {
			this.title = newValue.title;
			this.shareText = newValue.content;
			this.href = newValue.url;
			this.image = newValue.titlepic;
			this.shareType = newValue.shareType;
		}
	},
	onReady() {
		uni.getProvider({
			service: 'share',
			success: (e) => {
				let data = []
				for (let i = 0; i < e.provider.length; i++) {
					switch (e.provider[i]) {
						case 'weixin':
							data.push({
								name: '微信好友',
								id: 'weixin',
								zicon:"weixin",
								zclass:"wx",
								sort:0
							})
							data.push({
								name: '朋友圈',
								id: 'weixin',
								type:'WXSenceTimeline',
								zicon:"weixin",
								zclass:"pyq",
								sort:1
							})
							break;
						case 'sinaweibo':
							data.push({
								name: '新浪微博',
								id: 'sinaweibo',
								zicon:"xinlangweibo",
								zclass:"wb",
								sort:2
							})
							break;
						case 'qq':
							data.push({
								name: 'QQ好友',
								id: 'qq',
								zicon:"QQ",
								zclass:"qq",
								sort:3
							})
							break;
						default:
							break;
					}
				}
				this.providerList = data.sort((x,y) => {
					return x.sort - y.sort
				});
			},
			fail: (e) => {
				console.log('获取分享通道失败', e);
				uni.showModal({
					content:'获取分享通道失败',
					showCancel:false
				})
			}
		});
	},
	methods:{
		togle(){
			this.$emit('togle')
		},
		async share(e) {
			console.log('分享通道:'+ e.id +'； 分享类型:' + this.shareType);
			
			if(!this.shareText && (this.shareType === 1 || this.shareType === 0)){
				uni.showModal({
					content:'分享内容不能为空',
					showCancel:false
				})
				return;
			}
			
			if(!this.image && (this.shareType === 2 || this.shareType === 0)){
				uni.showModal({
					content:'分享图片不能为空',
					showCancel:false
				})
				return;
			}
			
			let shareOPtions = {
				provider: e.id,
				scene: e.type && e.type === 'WXSenceTimeline' ? 'WXSenceTimeline' : 'WXSceneSession', //WXSceneSession”分享到聊天界面，“WXSenceTimeline”分享到朋友圈，“WXSceneFavorite”分享到微信收藏     
				type: this.shareType,
				success: (e) => {
					console.log('success', e);
					uni.showModal({
						content: '分享成功',
						showCancel:false
					})
				},
				fail: (e) => {
					console.log('fail', e)
					uni.showModal({
						content: e.errMsg,
						showCancel:false
					})
				},
				complete:function(){
					console.log('分享操作结束!')
				}
			}
			
			switch (this.shareType){
				case 0:
					shareOPtions.summary = this.shareText;
					shareOPtions.imageUrl = this.image;
					shareOPtions.title = this.title;
					shareOPtions.href = this.href;
					break;
				case 1:
					shareOPtions.summary = this.shareText;
					break;
				case 2:
					shareOPtions.imageUrl = this.image;
					break;
				case 5:
					shareOPtions.imageUrl = this.image;
					shareOPtions.title = this.title;
					shareOPtions.miniProgram = {
						id:'gh_33446d7f7a26',
						path:'/pages/tabBar/component/component',
						webUrl:'https://uniapp.dcloud.io',
						type:0
					};
					break;
				default:
					break;
			}
			
			if(shareOPtions.type === 0 && plus.os.name === 'iOS'){//如果是图文分享，且是ios平台，则压缩图片 
				shareOPtions.imageUrl = await this.compress();
			}
			
			if (shareOPtions.provider === 'sinaweibo') {
				shareOPtions.type = 1;
				shareOPtions.imageUrl = '';
			}
			
			if (shareOPtions.provider === 'qq') {//如果是分享文字到qq，则必须加上href和title	
				shareOPtions.type = 1;
				shareOPtions.href = this.href;
				shareOPtions.title = this.title;
				
			}
			uni.share(shareOPtions);
		},
		compress(){//压缩图片 图文分享要求分享图片大小不能超过20Kb
			console.log('开始压缩');
			let img = this.image;
			return new Promise((res) => {
				var localPath = plus.io.convertAbsoluteFileSystem(img.replace('file://', ''));
				console.log('after' + localPath);
				// 压缩size
				plus.io.resolveLocalFileSystemURL(localPath, (entry) => {
					entry.file((file) => {// 可通过entry对象操作图片 
						console.log('getFile:' + JSON.stringify(file));
						if(file.size > 20480) {// 压缩后size 大于20Kb
							plus.zip.compressImage({
								src: img,
								dst: img.replace('.jpg', '2222.jpg').replace('.JPG', '2222.JPG'),
								width: '10%',
								height: '10%',
								quality: 1,
								overwrite: true
							}, (event) => {
								console.log('success zip****' + event.size);
								let newImg = img.replace('.jpg', '2222.jpg').replace('.JPG', '2222.JPG');
								res(newImg);
							}, function(error) {
								uni.showModal({
									content:'分享图片太大,需要请重新选择图片!',
									showCancel:false
								})
							});
						}
					});
				}, (e) => {
					console.log('Resolve file URL failed: ' + e.message);
					uni.showModal({
						content:'分享图片太大,需要请重新选择图片!',
						showCancel:false
					})
				});
			})
		}
	}
}
